html, body {
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  color: $text-gray;
  font-size: 10px;
  background-color: $white;
  @media (min-width: 425px) {
    font-size: 12px;
  }
  @media (min-width: 768px) {
    font-size: 14px;
  }
  @media (min-width: 1200px) {
    font-size: 16px;
  }
}
.title-blue {
  color: $blue;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0.5rem;
  margin-bottom: 0;
  position: relative;
  z-index: 1;
  @media (min-width: 768px) {
    margin-bottom: 3rem;
  }
  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6rem;
    height: 6rem;
    border-radius: 50%;
    background-color: $gray-light;
    z-index: -1;
  }
}
.redes {
  text-decoration: none;
  margin: 0 .2rem;
  .fa-stack {
    font-size: 1.5rem;
    .icon-bg {
      transition: all 0.3s;
      &.icon-in {
        color: #0e76a8;
      }
      &.icon-tw {
        color: #00acee;
      }
      &.icon-fb {
        color: #3b5998;
      }
      &.icon-ig {
        color: #d6249f;
      }
      &.icon-wa {
        color: #25D366;
      }
    }
    .icon-red {
      color: $white;
      font-size: 1.3em;
      transition: all 0.3s;
    }
  }
  &:hover {
    .icon-bg {
      &.icon-in {
        color: transparent;
      }
      &.icon-tw {
        color: transparent;
      }
      &.icon-fb {
        color: transparent;
      }
      &.icon-ig {
        color: transparent;
      }
      &.icon-wa {
        color: transparent;
      }
    }
    .icon-red {
      &.icon-in {
        color: #0e76a8;
      }
      &.icon-tw {
        color: #00acee;
      }
      &.icon-fb {
        color: #3b5998;
      }
      &.icon-ig {
        color: #d6249f;
      }
      &.icon-wa {
        color: #25D366;
      }
    }
  }
}
.modal {
  .modal-body {
    ul {
      li {
        margin-bottom: 1rem;
        &::marker {
          color: $blue;
        }
      }
    }
  }
}